<template>
	<view class="container">
		<!-- 一级类目 -->
		<view class="category-level-1">
			<view v-for="(season, index) in seasons" :key="index" class="season-item"
				:class="{ 'active': currentSeasonIndex === index }" @click="selectSeason(index)">
				{{ season }}
			</view>
		</view>

		<!-- 二级类目（横向滑动） -->
		<view class="category-container">
			<scroll-view class="category-level-2" scroll-x>
				<view class="category-wrapper">
					<view v-for="(category, index) in categories" :key="index" class="category-item"
						:class="{ 'active': currentCategoryIndex === index }" @click="selectCategory(index)">
						{{ category.name }}
					</view>
				</view>
			</scroll-view>
			<!-- 添加类目按钮 -->
			<image src="/static/add.png" class="add-category-btn" @click="showAddCategoryModal">+ </image>
		</view>


		<!-- 衣物列表 -->
		<scroll-view class="clothes-list" scroll-y>
			<!-- 如果没有过滤到衣物，显示空态页 -->
			<view v-if="filteredClothesRows.length === 0" class="empty-state">
				<text class="empty-state-text">当前没有衣物，请点击右下角按钮添加衣服。</text>
			</view>
			<view v-else class="clothes-list-content">
				<view class="clothes-row" v-for="(row, rowIndex) in filteredClothesRows" :key="rowIndex">
					<view class="clothes-item" v-for="(item, itemIndex) in row" :key="itemIndex">
						<image class="clothes-image" :src="item.image" mode="aspectFill" />
						<text class="clothes-time">{{ item.purchaseDate }}</text>
						<text class="clothes-name">{{ item.name }}</text>
					</view>
					<!-- 用占位符填充，使每行始终保持 3 列 -->
					<view class="clothes-item placeholder" v-for="(n, index) in (3 - (row?.length || 0))" :key="'placeholder' + index">
					</view>
				</view>
			</view>
		</scroll-view>

		<!-- 添加类目弹窗 -->
		<view class="modal-mask" v-if="showModal">
			<view class="modal">
				<text class="modal-title">添加类目</text>
				<input class="modal-input" v-model="newCategoryName" placeholder="请输入类目名称" />
				<text class="error-msg" v-if="errorMsg">{{ errorMsg }}</text>
				<view class="modal-buttons">
					<button class="cancel-btn" @click="closeModal">取消</button>
					<button class="confirm-btn" @click="addCategory">确定</button>
				</view>
			</view>
		</view>

		<!-- 右下角浮动按钮 -->
		<image src="/static/add.png" class="floating-btn" @click="onFloatingButtonClick"></image>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				currentSeasonIndex: 0,
				seasons: ['全部', '春', '夏', '秋', '冬'],
				currentCategoryIndex: 0,
				categories: [{
						name: '全部'
					},
					{
						name: '上衣'
					},
					{
						name: '裤子'
					},
					{
						name: '裙子'
					},
					{
						name: '鞋'
					},
					{
						name: '配饰'
					}
				],
				clothes: [],
				showModal: false, // 控制弹窗显示
				newCategoryName: "", // 输入的类目名称
				errorMsg: "", // 错误提示
				filteredClothes: [],
				filteredClothesRows: []
			};
		},
		onShow() {
			this.clothes = uni.getStorageSync('clothes') || [];
			this.filteredClothesBycategory();
		},
		methods: {
			filteredClothesBycategory() {

				const selectedSeason = this.seasons[this.currentSeasonIndex];
				const selectedCategory = this.categories[this.currentCategoryIndex].name;

				if (this.clothes.length === 0)
					return;

				if (selectedSeason === '全部' && selectedCategory === '全部') {
					this.filteredClothes = this.clothes;
				} else if (selectedSeason === '全部') {
					this.filteredClothes = this.clothes.filter(cloth => cloth.secondaryCategory === selectedCategory);
				} else if (selectedCategory === '全部') {
					this.filteredClothes = this.clothes.filter(cloth => cloth.primaryCategory === selectedSeason);
				} else {
					this.filteredClothes = this.clothes.filter(cloth => cloth.primaryCategory === selectedSeason && cloth
						.secondaryCategory ===
						selectedCategory);
				}

				this.filteredClothesWithRows();
			},
			filteredClothesWithRows() {
			    const rows = [];
			    let tempRow = [];
			
			    this.filteredClothes.forEach((item, index) => {
			        if (!item) return; // 避免 null 被添加
			        tempRow.push(item);
			        if (tempRow.length === 3 || index === this.filteredClothes.length - 1) {
			            rows.push([...tempRow]);
			            tempRow = [];
			        }
			    });
			
			    this.filteredClothesRows = rows.filter(row => row.length > 0); // 确保不存入空行
			},
			selectSeason(index) {
				this.currentSeasonIndex = index;
				this.filteredClothesBycategory();
			},
			selectCategory(index) {
				this.currentCategoryIndex = index;
				this.filteredClothesBycategory();
			},
			// 显示添加类目的弹窗
			showAddCategoryModal() {
				this.showModal = true;
				this.newCategoryName = "";
				this.errorMsg = "";
			},
			// 关闭弹窗
			closeModal() {
				this.showModal = false;
			},
			// 添加类目
			addCategory() {
				const newName = this.newCategoryName.trim();
				if (!newName) {
					this.errorMsg = "类目名称不能为空";
					return;
				}
				// 检查是否重复
				if (this.categories.some(cat => cat.name === newName)) {
					this.errorMsg = "该类目已存在，请输入其他名称";
					return;
				}
				// 添加新类目
				this.categories.push({
					name: newName
				});
				this.showModal = false; // 关闭弹窗
				uni.showToast({
					title: '添加类目成功！',
					icon: 'success'
				});
			},


			onFloatingButtonClick() {
				uni.navigateTo({
					url: '/pages/addClothes/addClothes'
				});

			}
		}
	};
</script>

<style scoped>
	/* 全局容器 */
	.container {
		display: flex;
		flex-direction: column;
		height: 100vh;
		background-color: #f9f9f9;
	}

	/* 右下角浮动按钮 */
	.floating-btn {
		position: fixed;
		right: 20px;
		bottom: 40px;
		/* 位于底部导航栏上方 */
		width: 50px;
		height: 50px;
		background: #ff9800;
		color: white;
		border-radius: 50%;
		text-align: center;
		line-height: 50px;
		font-size: 20px;
		box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.2);
		border: none;
		cursor: pointer;
	}

	/* 一级分类（季节） */
	.category-level-1 {
		display: flex;
		justify-content: space-around;
		padding: 12px 0;
		background-color: #fff;
		box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
	}

	.season-item {
		padding: 10px 15px;
		color: #666;
		cursor: pointer;
		border-radius: 10px;
		transition: all 0.2s;
	}

	.season-item.active {
		color: #fff;
		font-weight: bold;
		background-color: #d0e183;
	}

	/* 二级分类（横向滑动） */
	.category-container {
		display: flex;
		align-items: center;
		padding: 10px;
		background-color: #fff;
		border-bottom: 1px solid #eee;
	}

	.category-level-2 {
		flex-grow: 1;
		white-space: nowrap;
		overflow-x: auto;
	}

	.category-wrapper {
		display: flex;
	}

	.category-item {
		padding: 8px 15px;
		margin-right: 10px;
		border-radius: 5px;
		cursor: pointer;
		background-color: #eaeaea;
		color: #333;
		transition: all 0.2s;
		font-size: 14px;
	}

	.category-item.active {
		background-color: #d0e183;
		color: white;
		font-weight: bold;
	}

	/* 添加分类按钮 */
	.add-category-btn {
		width: 35px;
		height: 35px;
		line-height: 35px;
		color: #fff;
		border: none;
		background-color: #fff;
		color: white;
		border-radius: 10%;
		box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.2);
		position: relative;
		right: 0;
		padding: 3px;
		margin-left: 10px;
	}

	/* 空态页样式 */
	.empty-state {
		flex: 1;
		margin-top: 50px;
		display: flex;
		justify-content: center;
		align-items: center;
		text-align: center;
	}

	.empty-state-text {
		font-size: 18px;
		color: #d0e183;
	}

	/* 衣物列表 */
	.clothes-list {
		flex: 1;
	}

	/* 每一行 */
	.clothes-row {
		display: flex;
		justify-content: flex-start;
		margin-bottom: 10px;
	}

	/* 衣物卡片 */
	.clothes-item {
		width: 32%;
		margin: 1%;
		background-color: white;
		padding: 10px;
		border-radius: 10px;
		box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1);
		text-align: center;
		position: relative;
		transition: transform 0.2s ease-in-out;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		min-height: 120px;
	}

	.clothes-item.placeholder {
		visibility: hidden;
	}

	/* 图片 */
	.clothes-image {
		width: 70px;
		height: 70px;
		border-radius: 10px;
		margin-bottom: 5px;
	}

	/* 衣物时间 */
	.clothes-time {
		font-size: 12px;
		color: #999;
		position: absolute;
		bottom: 5px;
		width: 100%;
		text-align: center;
	}

	/* 衣物名称 */
	.clothes-name {
		font-size: 14px;
		color: #333;
		margin-bottom: 5px;
	}

	/* 弹窗背景 */
	.modal-mask {
		position: fixed;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background: rgba(0, 0, 0, 0.5);
		display: flex;
		justify-content: center;
		align-items: center;
	}

	/* 弹窗内容 */
	.modal {
		width: 80%;
		background: white;
		padding: 20px;
		border-radius: 10px;
		box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.2);
		text-align: center;
	}

	/* 标题 */
	.modal-title {
		font-size: 18px;
		font-weight: bold;
		color: #333;
		margin-bottom: 10px;
	}

	/* 输入框 */
	.modal-input {
		width: 90%;
		padding: 10px;
		margin: 10px 0;
		border: 1px solid #ddd;
		border-radius: 5px;
		font-size: 16px;
		text-align: center;
	}

	/* 错误提示 */
	.error-msg {
		color: red;
		font-size: 14px;
		margin-bottom: 10px;
	}

	/* 按钮区域 */
	.modal-buttons {
		display: flex;
		justify-content: space-around;
		margin-top: 10px;
	}

	/* 取消按钮 */
	.cancel-btn {
		padding: 0px 20px;
		background: #d4d4d4;
		color: #333;
		border: none;
		border-radius: 5px;
		font-size: 16px;
		cursor: pointer;
	}

	/* 确定按钮 */
	.confirm-btn {
		padding: 0px 20px;
		background: #d0e183;
		color: white;
		border: none;
		border-radius: 5px;
		font-size: 16px;
		cursor: pointer;
	}
</style>